import constants from './constants.js'
import {randomId} from './utils.js'

import { Decimal } from 'decimal.js'
Decimal.set({ precision: 13, rounding: 6 })
window.Decimal = Decimal


function meanAndSd(arr) {
  let n = arr.length
  let mean = arr.reduce((a, c) => a + c, 0) / n
  let sd = Math.sqrt(arr.map(x => Math.pow(x - mean, 2)).reduce((a, c) => a + c) / n, 0)

  return [mean, sd]
}

function roundHistY(y) {
  return Math.round(y*100)/100
}

const interpolate = (y1, y2, x1, x2, x) => {
  return y1 + ((x - x1)/(x2 - x1)) * (y2 - y1)
}


export default {
  methods: {
    inputFieldInteraction (event, args, arg_i) {
      if (args.length > 3 && event.target.value === '' && ['Backspace', 'Delete'].indexOf(event.key) !== -1) {
        args.splice(arg_i, 1)
        event.target.blur()
        
        // avoid backspace delete on focus
        this.$nextTick(() => {
          window.setTimeout(() => {
            if (event.target) event.target.focus()
          }, 100)
        })
      }
    },
    removeCurrentWb () {
      if (!window.confirm('Confirm remove this workbook?')) return
      if (!window.confirm('Are you sure?')) return

      // remove cells
      this.notebook.cells = this.notebook.cells.filter(cell => {
        return cell.wb !== this.notebook.currentWb
      })
      
      // remove workbook
      let currentWbObjIndex = this.notebook.wbs.map(wb => wb.id).indexOf(this.notebook.currentWb)
      this.notebook.wbs.splice(currentWbObjIndex, 1)

      this.notebook.currentWb = this.notebook.wbs[0].id


    },
    newWb () {
      let wbBase = {
        id: randomId(),
        name: 'New workbook',
        date: (new Date(Date.now())).toISOString().split('T')[0],
        due: undefined
      }

      this.notebook.wbs.push(Object.assign({}, constants.WB, wbBase))
    },
    previousPage () {
      if (this.currentPage > 0) {
        this.currentPage -= 1
      }
    },
    nextPage () {
      if (this.currentPage < this.nPages - 1) {
        this.currentPage += 1
      }
    },
    calcCell (i) { // eslint-disable-line no-unused-vars
      if (this.stateControl.processing) return
      this.stateControl.processing = true
      let cell = this.notebook.cells[i]

      //const HIST_SIZE = constants.HIST_SIZE
      const N_BURSTS = constants.N_BURSTS
      const BURST_SIZE = constants.BURST_SIZE

      // init monitoring params
      var M = 0

      // this variable will be averaged in-place
      // first is undefined, than will be the array
      let resAverage = undefined

      const Mend = BURST_SIZE * N_BURSTS * constants.POOL_SIZE

      // compute realtime parameters average
      this.workerPool.forEach((worker) => {

        if (!this.stateControl.processing) return

        worker.onerror = () => {

          if (!this.stateControl.processing) return
          this.stateControl.processing = true
          alert('Error. Check provided expression and parameters.')

          // recovery worker pool
          this.mc.terminate()
          this.workerPool = this.mc.init()
          this.mc.waitReadyStatus(() => this.stateControl.processing = false)

        }

        worker.onmessage = (e) => {
          // wasm function return only one array with values:
          //
          // [0]  : lower probabilistic coverage interval 
          // [1]  : higher probabilistic coverage interval 
          // [2]  : the average value
          // [3]  : min value
          // [4]  : max value
          // [5..]: histogram
          let res = [].slice.call(e.data)

          resAverage = resAverage || new Array(res.length).fill(0)

          // weigth average in-place the array 
          resAverage = resAverage.map((item, i) => (item * M + res[i] * BURST_SIZE) / (M + BURST_SIZE))
          
          // TODO: Implement adaptative aproach
          //console.log(resAverage[0])
          
          M += BURST_SIZE
          
                    
          // process only at end or if intermediate result
          if ((M !== Mend) && (M !== BURST_SIZE)) return

          // update chart with itermediate results
          // if chart already exists, prevent the chart to animate futher
          if ((M === BURST_SIZE) && this.histCharts[cell.id]) {
            this.setHistogram(cell, false, 'rgba(079, 104, 155, .4)')
            return
          }

          let histFreqRaw = resAverage.slice(6)
          let [min, max] = [resAverage[3], resAverage[4]]
          cell.min = min
          cell.max = max
          cell.hist = histFreqRaw.map(y => roundHistY(y))

                    
          //this.plotHistogram(cell, histChart, min, max)
          this.setCellResults(cell, resAverage)
          
          // update chart with final results
          this.setHistogram(cell)

          this.stateControl.processing = false
        }
      })

      let payload = this.buildPayload(cell, this.expressionsVars[i])

      for (let i = 0; i < N_BURSTS; i++) {
        this.workerPool.forEach(worker => {
          worker.postMessage(payload)
        })
      }

    },
    preFill () {
      this.$nextTick(() => {
        this.expressionsVars.forEach((rowVars, i) => {
          let cell = this.notebook.cells[i]
          //console.log(rowVars, i, cell)

          cell.data = cell.data || []

          rowVars.forEach(rowVar => {
            let quantity = cell.data.filter(item => item.var === rowVar)[0] || cell.data[cell.data.push({}) - 1]
            quantity.var = quantity.var || rowVar
            quantity.dist = quantity.dist || "Uniform"
            quantity.args = quantity.args || [-1, 1, 0]
          })
        })
      })
    },
    distChange (quantity) {
      let initArgs = {
        'Uniform': [-1, 1, 0],
        'Normal': [0, 1, 0],
        'StudentsT': [0, 1, 1000],
        'Triangular': [-1, 1, 0],
        'Arcsine': [-1, 1, 0],
        'Trap': [-1, 1, 0.5],
        'CTrap': [-1, 1, 2],
        'Input': [undefined, undefined, undefined],
        'Resol': [0.0001, undefined, undefined],
      }
      if (initArgs[quantity.dist]) {
        quantity.args = Array.from(initArgs[quantity.dist])
      }
    },
    arrayMove (oldIndex, newIndex) {
      //https://stackoverflow.com/questions/5306680/move-an-array-element-from-one-array-position-to-another
      let arr = this.notebook.cells
      if (newIndex >= arr.length) {
        var k = newIndex - arr.length + 1
        while (k--) {
          arr.push(undefined)
        }
      }
      arr.splice(newIndex, 0, arr.splice(oldIndex, 1)[0])
      return arr
    },
    createCell () {
      var cell = {
        id: randomId(),
        //time: (new Date(Date.now())).toISOString(),
        expr: 'X1',
        unit: '',
        wb: this.notebook.currentWb
      }
      
      // some filters when active, set a scope os creation for new cells
      this.filtersCreated.filter(f => f[0] === '_HasTag ?').forEach(item => {
        cell.tags = cell.tags || []
        cell.tags.push(item[1])
      })

      this.filtersCreated.filter(f => f[0] === '_Unit =').forEach(item => {
        cell.unit = item[1]
      })

      this.notebook.cells.push(cell)
      this.preFill()
    },
    removeCell (i) {
      if (!window.confirm('Confirm remote this cell?')) return
      this.notebook.cells.splice(i, 1)
    },
    setCellResults(cell, resAverage) {
      // compute distribution limits and results
      // standard deviation
      const sdDec = new Decimal(resAverage[5])

      // lower probabilistioc limit
      const pcilDec = new Decimal(resAverage[0])

      // high probabilistioc limit
      const pcihDec = new Decimal(resAverage[1])
      const yDec = new Decimal(resAverage[2])

      // Significance aritimetic
      let nSD = 2
      let exp = parseFloat((pcihDec.sub(pcilDec)).abs().toSD(2).toExponential().split('e')[1])
      let pcPrecision = Math.abs(exp)+nSD-1
      
      cell.sd = sdDec.toFixed(pcPrecision).toString()
      cell.pcil = pcilDec.toFixed(pcPrecision).toString()
      cell.pcih = pcihDec.toFixed(pcPrecision).toString()
      cell.y = yDec.toFixed(pcPrecision).toString()
    },
    buildPayload(cell, expressionsVars) {
      // prepare arguments to send to wasm function
      let payload = JSON.parse(JSON.stringify(cell))
      payload.data = payload.data.filter(item => expressionsVars.indexOf(item.var) !== -1)
      payload.p = 0.95

      // piecewise dists are expected to be the string
      // defining the distribution, so we match it
      payload.data.forEach(item => {

        // some quantities dist type as for building convenient UI, so we need to transform it
        // to somre proper PDF information
        if (item.dist === 'Input') {
          let [mean, sd] = meanAndSd(item.args.map(parseFloat))
          if (Math.abs(sd) < constants.JS_ZERO) {
            // TODO: Create a constant distribution type for such cases
            item.dist = 'Uniform'
            item.args = [mean, mean, 0]
          } else {
            item.dist = 'Normal'
            item.args = [mean, sd, 0]
          }
        }

        if (item.dist === 'Resol') {
          let resol = parseFloat(item.args[0])
          item.dist = 'Uniform'
          item.args = [-resol/2, resol/2, 0]
        }

        if (this.isSavedQuery(item.dist)) {
          let filter = item.dist
          let selectedCells = this.notebook.cells.filter(cell => cell.wb === item.args[0])
          let cellsFilterObj = this.filterIteratorReducer(selectedCells, filter)
          let cells = selectedCells.filter(cell => cellsFilterObj[cell.id])

          if (cells.length === 0) {
            alert('Query return no cells.')
            return
          }
          
          // desired expectation

          let x = this.cellsVarValuesObjects[cell.id][item.args[1]]
          
          let closestCells = cells.map(cell => [Math.abs(x - parseFloat(cell.y)), cell]).sort((a, b) => a[0]-b[0]).map(pairs => pairs[1]).slice(0, 2)
          let x1 = parseFloat(closestCells[0].y)
          
          let interpolatedCellSubset
          if ((cells.length === 1) || (x < x1)) {
            // extrapolating down should result at least with same uncetanty of first item

            interpolatedCellSubset = {
              min: closestCells[0].min,
              max: closestCells[0].max,
              hist: closestCells[0].hist
            }
            
          } else {
            let x2 = parseFloat(closestCells[1].y)

            // interpolate parametesr to build a histogram
            // TODO: look for advice it this is statistical correct
            interpolatedCellSubset = {
              min: interpolate(closestCells[0].min, closestCells[1].min, x1, x2, x),
              max: interpolate(closestCells[0].max, closestCells[1].max, x1, x2, x),
              hist: closestCells[0].hist.map((y1, i) => {
                let y2 = closestCells[1].hist[i]
                return roundHistY(interpolate(y1, y2, x1, x2, x))
              })
            }
          }

          let piecewiseString = this.buildPiecewiseString(interpolatedCellSubset)
          item.dist = piecewiseString
          item.args = [0, 0, 0]

        }

        let piecewiseMatch = this.exportedPiecewise.map(x => x.id).indexOf(item.dist) !== -1
        if (piecewiseMatch) {
          let piecewiseMatchCell = this.notebook.cells.filter((cell) => cell.id == item.dist)[0]
          let piecewiseString = this.buildPiecewiseString(piecewiseMatchCell)
          item.dist = piecewiseString
        }
      })
      
      return payload
    },
    evalLimitExpr (cell) {

      if (!cell.limitType) {
        delete cell.limL
        delete cell.limH
        delete cell.limHExpr
        delete cell.limLExpr
        return
      }

      let varParams = this.cellsVarValuesTuples[cell.id].map(item => item[0])
      let varValues = this.cellsVarValuesTuples[cell.id].map(item => item[1])

      try {

        cell.limH = window.MC.simple_eval({expr: cell.limHExpr, var_params: varParams, var_values: varValues})
        if (cell.limitType === 'asym') {
          cell.limL = window.MC.simple_eval({expr: cell.limLExpr, var_params: varParams, var_values: varValues})
        } else {
          cell.limL = -cell.limH
        }
        this.setHistogram(cell)
        this.setTrendChart()
      }
      catch (e) {
        //console.log(e)
      }
    }
  },
  computed: {
  }
}