function isArray(o) {
  return Object.prototype.toString.call(o) === '[object Array]'; 
}


export default {
  methods: {
    getQueryDisplayText (filters) {
      //                                                   
      return filters.map(x => x.reduce((a, c) => 
        a+' '+
        (c[0]==='_' ? c.slice(1) : c), // remove starting "_"
      '')).toString()
    },
    isSavedQuery (filter) {
      return isArray(filter[0]) //&&
        //(this.savedQueriesDisplayText.indexOf(this.getQueryDisplayText(filter[0])) !== -1)
    },
    saveQuery () {
      let text = this.getQueryDisplayText(this.filtersCreated)

      if (this.savedQueriesDisplayText.indexOf(text) !== -1) {
        window.alert('Query already exists.')
        return
      }

      this.notebook.queries.push(JSON.parse(JSON.stringify(this.filtersCreated)))
      window.alert('Query saved.')

    },
    filtersPreCallable (filters) {

      // concat selected saved queries, that are identified by the argument c[0] being an array
      return filters.filter(item => item[0] !== undefined).reduce((a, c) => {
        isArray(c[0]) ? a.concat(c[0]) : a


        // if first element of array is a array, its a saved query, 
        // so we concat its content ad index 0
        if (isArray(c[0])) {
          return a.concat(c[0])
          
        }
        // and others are regular filters
        return a.concat([c])
        
        }, [])
        .map(item => item[0].split(' ').concat(item[1]))

    },
    filterIteratorReducer (selectedCells, filters) {
      let filtersToUse = filters ? this.filtersPreCallable(filters) : this.filtersPre
      let idsToShow = {}

      selectedCells.forEach(cell => {
        idsToShow[cell.id] = filtersToUse.reduce((a, c) => {
          let lhs  = c[0] // left hand side
          let oper = c[1] // comparison operator
          let argStr  = c[2] // argument
          let argFloat  = parseFloat(c[2]) // argument

          let tuples = this.cellsVarValuesTuples[cell.id]

          // when we have a filter defined and try to create a new cell, `tuples`
          // is undefined, so we just return here and wait the next render
          if (!tuples) return a

          if (lhs === '_Unit') {
            return a && cell.unit === argStr
          // } else if (oper === '=') {
          //   return a && tuples.filter(v => (v[0] === lhs) && v[1] === argFloat).length
          
          // time filter
          // } else if ((oper === '≤') && (lhs === '_CellDate')) {
          //   console.log()
          //   return a &&  cell.time.split('T')[0] <= argStr
          
          // } else if ((oper === '≥') && (lhs === '_CellDate')) {
          //   return a &&  cell.time.split('T')[0] >= argStr

          // var result filter
          } else if (oper === '≤') {
            return a && tuples.filter(v => (v[0] === lhs) && v[1] <= argFloat).length
          } else if (oper === '≥') {
            return a && tuples.filter(v => (v[0] === lhs) && v[1] >= argFloat).length
          
          // tag filter
          } else if (lhs === '_HasTag' && oper === '?') {
            return a && cell.tags && cell.tags.length && (cell.tags.indexOf(argStr) !== -1)
          // ...
          } else {
            return a
          }
        }, true)
      })
      return idsToShow
    },
    conditionChangeSelect (event, fi) {

      // clear input when changing the string-based argument criteria
      if (['_HasTag ?', '_Unit ='].indexOf(this.filtersCreated[fi][0] !== -1)) {
        this.filtersCreated[fi][1] = undefined
        this.currentPage = Math.min(this.currentPage, this.nPages)
      }
    },
    newFilter () {
      this.filtersCreated.push([undefined, ''])
    }
  },
  computed: {
    nFiltered () {
      return Object.keys(this.filteredData).reduce((a, c) => a + (!this.filteredData[c] ? 1 : 0), 0)
    },
    nShown () {
      return Object.keys(this.filteredData).reduce((a, c) => a + (!this.filteredData[c] ? 0 : 1), 0)
    },
    shownCells () {
      return this.notebook.cells.filter(cell => this.filteredData[cell.id])
    },
    nPages () {
      return Math.ceil(this.nShown/this.perPage)
    },
    currentPageCells () {
      return this.shownCells.slice(this.currentPage * this.perPage, this.currentPage * this.perPage + this.perPage).reduce((a, c) => {
        a[c.id] = true
        return a
      }, {})
    },
    paginationPadding () {
      return 1
    },
    paginationAtStart () {
      return this.currentPage - this.paginationPadding <= 0
    },
    paginationAtEnd () {
      return this.currentPage + this.paginationPadding >= this.nPages
    },
    paginationFirstVisible () {
      return this.currentPage - this.paginationPadding - 1 < 0
    },
    paginationLartVisible () {
      return this.currentPage + this.paginationPadding + 1 >= this.nPages
    },
    pagesRange () {
      let start
      if (this.paginationAtStart) {
        start = 0
      } else if (this.paginationAtEnd) {
        start = this.nPages - 1 - this.paginationPadding * 2
      } else {
        start = this.currentPage - this.paginationPadding
      }
      return new Array(1 + this.paginationPadding * 2).fill(0).map((_, i) => i+start)
    },
    filtersPre () {
      return this.filtersPreCallable(this.filtersCreated)
    },
    filteredData () {
      let selectedCells = this.notebook.cells.filter(cell => {

        // only current workbook
        return cell.wb === this.notebook.currentWb
      })
      return this.filterIteratorReducer(selectedCells)
    },
    filteredDataAllWb () {
      return this.filterIteratorReducer(this.notebook.cells)
    },
    avaliableFilters () {
      return this.expressionsVars
        .reduce((a, c) => a.concat(c),[]) // flatten
        .reduce((a, c) => {if (a.indexOf(c) === -1) a.push(c); return a}, []) // uniq
        .map(item => [
          item + ' ≥',
          item + ' ≤',
          //item + ' ='
        ]) // conditions
        .reduce((a, c) => a.concat(c),[]) // flatten again      
        
        //.concat(this.units.reduce((a, c) => {if (a.indexOf(c) === -1) a.push(c); return a}, [])) // uniq units
    },
    savedQueriesDisplayText () {
      return this.notebook.queries.map(item => {
        return this.getQueryDisplayText(item)
      })
    },
    isUsingSavedQuery () {
      return this.filtersCreated.reduce((a, c) => {
        return this.isSavedQuery(c) || a
      }, false)
    },
    savedFiltersTuples () {
      return this.notebook.queries.map((item, i) => {
        return [this.savedQueriesDisplayText[i], item]
      })
    }
  }
}