export default {
  methods: {
    removeTag (event, cell, tag_i) {
      cell.tags.splice(tag_i, 1)
      
      let replaceDatasets = true
      this.setTrendChart(replaceDatasets) 

    },
    newTag (event, cell) {
      cell.tags = cell.tags || [];cell.tags.push('')
      this.$nextTick(() => {
        event.target.previousElementSibling.querySelector('input').focus()
      })
    },
    setTag (event, cell, tag_i) {
      
      // in esc press, unset item
      if (event.key === 'Escape') {
        event.target.value = ''
      } else {
        
        // if leave empty on blur, unset
        if (event.target.value.trim() === '') {
          event.target.value = ''        
        } else {

          // if press enter, dont set value by programaticaly
          // as it will already be set by some native behaviour
          if (event.key !== 'Enter') {
            // set the value
            cell.tags[tag_i] = event.target.value
          }
        }
      }
      
      if (!cell.tags) return

      // uniq itens
      cell.tags = cell.tags.reduce((a, c) => {
        if (a.indexOf(c) === -1) a.push(c)
        return a
      }, [])
      // sort string
      .sort((a, b) => a.localeCompare(b))
      .filter(x => x && x.length)

      let replaceDatasets = true
      this.setTrendChart(replaceDatasets) 
    },
    setTagSize (event) {
      if (event.target.value) {
        if (event.target.value.length > 30) {
          event.target.style.width = '500px'
          return
        }        
        if (event.target.value.length > 10) {
          event.target.style.width = '230px'
          return
        }
      }
    }
  },  
  computed: {
    visibleCellsByTagsGroups () {
      return this.notebook.cells.reduce((a, c) => {

        // show only filtered data
        if (!this.filteredData[c.id]) return a
        
        // if (!c.tags || !c.tags.length) return a
        
        let tagsStr
        
        if (!c.tags || !c.tags.length) {
          tagsStr = 'untagged'
        } else {
          tagsStr = c.tags.toString()
        }

        let match = a.filter(item => item.name === tagsStr)
        
        let group
        
        if(!match.length) {
          group = {name: tagsStr, cells: []}
          a.push(group)
        } else {
          group = match[0]
        }

        group.cells.push(c)

        return a
      }, [])
    },
    uniqTags () {
      return Object.keys(this.tagsCount)
    },
    uniqTagsByCell () {
      return Object.keys(this.tagsCount).reduce((a, c) => this.tagsCount[c] === 1 ? !a.push(c)||a : a, [])
    },
    tagsCount () {
      return this.notebook.cells.reduce((a, c) => {
        if (!c.tags) return a

        c.tags.forEach(tag => {
          a[tag] = a[tag] ? a[tag] + 1 : 1
        })
        return a
      }, {})
    }
  }
}