<template>
  <input 
    v-model="value"
    class="input" 
    ref="input"
  >
</template>

<script>
export default {
  name: 'number-input',
  props: ['modelValue'],
  emits: ['update:modelValue'],
  data () {
    return {
      prevValue: ''
    }
  },
  computed: {
    value: {
      get() {
        return this.modelValue
      },
      set(value) {
        if (!valueFmt) this.$emit('update:modelValue', '')
        let valueFmt = value.replaceAll(',', '.').split('').filter(c => !isNaN(+c) || ['-', '+', '.', 'e', 'E'].indexOf(c) !== -1).join('')

        if (isNaN(+valueFmt)) {
          
          // edge cases
          
          // let eCount = valueFmt.split('').reduce((a, c) => a + ('e' === c.toLowerCase() ? 1 : 0), 0)

          // let charCount = valueFmt.split('').reduce((a, c) => a + ((isNaN(+c) || ['-', '+', '.'].indexOf(c) !== -1) ? 1 : 0), 0)
          
          // if ((valueFmt.length >= 2) &&  (eCount === 1) && ((charCount-eCount) <= 1)) {
          //   this.prevValue = valueFmt
          //   this.$emit('update:modelValue', valueFmt)
          //   return
          // }


          // if (this.$refs.input.value !== this.prevValue) {
          //   this.$refs.input.value = this.prevValue
          // }
          // this.$emit('update:modelValue', this.prevValue)
          // return
        }
        if (value !== valueFmt) {
          this.$refs.input.value = valueFmt
        }
        //this.prevValue = valueFmt
        this.$emit('update:modelValue', valueFmt)
      }
    }
  }
}
</script>

