import constants from './constants.js';
import NumberInput from './components/NumberInput.vue'
// import DateInput from './components/DateInput.vue'
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/themes/dark.css'

import MC from './mc.js';

import filters from './filters.js'
import tags from './tags.js'
import histogram from './histogram.js'
import dataIO from './dataIO.js'
import interactions from './interactions.js'
import information from './information.js'
import charts from './charts.js'

import feather from 'feather-icons'
import {JSONUncrush} from './JSONCrush.js'
import {randomId} from './utils.js'

export default {
  name: 'App',
  components: {
    NumberInput,
    flatPickr
  }, 
  watch: {
    
    // update trend charts trigger
    'filteredData': {
      handler () {
        // net tick to wait for `preFill` call
        this.$nextTick(() => {
          let replaceDatasets = true
          this.setTrendChart(replaceDatasets)
        })
      }
    },
    'chartType': {
      handler () {
        // net tick to wait for `preFill` call
        this.$nextTick(() => {
          let replaceDatasets = true
          this.setTrendChart(replaceDatasets)
        })
      }
    },
    'chartSelectedVar': {
      handler () {
        let replaceDatasets = true
        this.setTrendChart(replaceDatasets)
      }
    },
    'chartTrendCellId': {
      handler () {
        this.$nextTick(() => {
          let replaceDatasets = true
          this.setTrendChart(replaceDatasets)
        })
      }
    },
    'trendChartCells': {
      handler () {
        this.$nextTick(() => {
          let replaceDatasets = true
          this.setTrendChart(replaceDatasets)
        })
      }
    },
    'stateControl.processing': {
      handler (val, prevVal) {
        if (val === false && prevVal === true) {
          this.setTrendChart()
        }
      },
    }
    // notebook: {
    //   handler (val) { // eslint-disable-line no-unused-vars
    //     this.preFill()
    //   },
    //   deep: false
    // },
  },
  data () {
    const firstWbId = randomId()
    return {
      notebook: {
        cells: [],
        currentWb: firstWbId,
        wbs: [Object.assign({}, constants.WB, {id: firstWbId, date: (new Date(Date.now())).toISOString().split('T')[0]})],
        queries: []
      },
      histCharts: {},
      stateControl: { processing: true },
      permalink: '',
      filtersCreated: [],
      workerPool: null,
      mc: null,
      trendChart: null,
      chartType: 'None', // None,Range,Trend
      chartSelectedVar: undefined,
      currentPage: 0,
      perPage: 5,
      chartTrendCellId: undefined
    }
  },

  /*
  methods
  */

  methods: Object.assign({}, filters.methods, tags.methods, histogram.methods, dataIO.methods, interactions.methods, information.methods, charts.methods),

  /*
  computed
  */

  computed: Object.assign({}, filters.computed, tags.computed, /*histogram.computed, dataIO.computed, interactions.computed,*/ information.computed, charts.computed),
  
  mounted () {
    this.$refs.loadDataInput.addEventListener('change', this.loadData, false);
    //this.$refs.loadDataInput.addEventListener('update', this.loadData, false);

    let json = (window.location.href.split('/#/').slice(1).join('/#/'))
    if (json.length) {
      try {
        //this.notebook = JSON.parse(decodeURI(json))
        this.notebook = JSON.parse(JSONUncrush(decodeURIComponent(json)))
        //console.log(this.notebook)
      } catch (e) {
        console.warn('Unable to parse url input.')
        console.log(json)
      }
    }
    this.preFill()

    this.setHistogramAll()
    
    this.setTrendChart()

    // icons
    feather.replace()

    // simulation handler

    this.mc = new MC()
    this.workerPool = this.mc.init()
    
    
    this.mc.waitReadyStatus(() => this.stateControl.processing = false)

  }
}
