import Worker from 'worker-loader!./worker.js';
import constants from './constants.js';

export default function function_name() {
  // array to indicate workers in pool are ready
  var workerPoolReadyFlag = new Array(constants.POOL_SIZE).fill(false);

  var workerPool
  
  this.init = () => {

    // start workers
    workerPool = new Array(constants.POOL_SIZE).fill(undefined).map((_, i) => { // eslint-disable-line no-unused-vars
      var worker = new Worker;
      
      worker.onmessage = function(e) {
        workerPoolReadyFlag[e.data] = true
      }
      
      worker.postMessage({ init: true, i: i });
      return worker
    })

    return workerPool
  }
  
  // check if all workers are aready to use
  this.waitReadyStatus = (cb) => {
    if (workerPoolReadyFlag.reduce((a, c) => a && c, true)) {
      if (cb) cb()
    } else {
      window.setTimeout(() => {
        this.waitReadyStatus(cb)
      }, 100)
    }
  }
 
  this.terminate = () => {
    workerPool.forEach(item => {
      item.terminate()
    })
  }
 return this  
}