import constants from './constants.js';
import { Chart } from 'chart.js';
import ChartHist from './chart-histogram.js';

// import { Chart, BarController, BarElement, CategoryScale, LineController, LineElement, PointElement, LinearScale, Title } from 'chart.js'

// Chart.register(BarController, BarElement, CategoryScale, LineController, LineElement, PointElement, LinearScale, Title);

import { annotation } from 'chartjs-plugin-annotation';

Chart.plugins.register({
  annotation: annotation
});

export default {
  methods: {
    setHistogramObject (cell) {
      
      // pointer to charts array
      var histCharts = this.histCharts
      
      // get or create histrochart
      histCharts[cell.id] = histCharts[cell.id] || new Chart(this.$refs['hist_'+cell.id].getContext('2d'), ChartHist(cell.hist)); // chart
      return histCharts[cell.id]
    },
    setHistogram (cell, withAnnotations, backgroundColor) {
      
      // when notebook is loaded but no hist avaliable
      if (!cell.hist) return
      
      if (withAnnotations === undefined) {withAnnotations = true}
      backgroundColor = backgroundColor === undefined ? 'rgba(179, 204, 255, .4)' : backgroundColor

      let histChart = this.setHistogramObject(cell)

      // set chart data
      let data = cell.hist.map(x => x)
      let histChartLabels = this.computeHistogramLabels(cell)

      let barSize = ((cell.max-cell.min)/constants.HIST_SIZE)
      let halfBar = barSize/2
      let scale = constants.HIST_SIZE/(cell.max-cell.min)

      let paddingLeft = 0
      let paddingRight = 0

      // set annotations only if they data is defined
      if (withAnnotations && (cell.min !== undefined) && (cell.max !== undefined)) {
        
        
        // limits 
        if (cell.limitType) {

          histChart.annotation.elements['a-line-3'].options.value = (cell.limL - cell.min - halfBar + paddingLeft) * scale
          //histChart.annotation.elements['a-line-3'].options.label.xAdjust = +15// xPadding xAdjust
          //histChart.annotation.elements['a-line-3'].options.label.content = cell.pcih

          histChart.annotation.elements['a-line-4'].options.value = (cell.limH - cell.min - halfBar + paddingRight) * scale
          //histChart.annotation.elements['a-line-4'].options.label.xAdjust = -15// xPadding xAdjust
          //histChart.annotation.elements['a-line-4'].options.label.content = cell.pcih

          
        } else {
          delete histChart.annotation.elements['a-line-3']
          delete histChart.annotation.elements['a-line-4']
        }

        // NOTE: important do keep `pcil` and `pcih` string to avoid strange chart annimation
        let pcilScaled = scale * (cell.pcil - cell.min - halfBar + paddingLeft)
        histChart.annotation.elements['a-line-1'].options.value = pcilScaled
        histChart.annotation.elements['a-line-1'].options.label.xAdjust = +15// xPadding xAdjust
        histChart.annotation.elements['a-line-1'].options.label.content = cell.pcil

        let pcihScaled = scale * (cell.pcih - cell.min - halfBar + paddingRight)
        histChart.annotation.elements['a-line-2'].options.value = pcihScaled
        histChart.annotation.elements['a-line-2'].options.label.xAdjust = -15// xPadding xAdjust
        histChart.annotation.elements['a-line-2'].options.label.content = cell.pcih

      }


      histChart.data.labels = histChartLabels
      histChart.data.datasets[0].data = data
      histChart.data.datasets[0].backgroundColor = backgroundColor

      histChart.update()
      
      return histChart
    },
    computeLabelval (cell, i) {
      return cell.min + i * (cell.max-cell.min)/constants.HIST_SIZE
    },
    computeHistogramLabels (cell) {
      let histChartLabels = []
      
      cell.hist.forEach((freq, i) => {
        let labelVal = this.computeLabelval(cell, i)
        
        // update chart labels and data
        histChartLabels.push((Math.abs(labelVal) < 0.1 ? labelVal.toExponential(3) : labelVal.toFixed(3)) + (' ' + cell.unit || ''))
      })
      return histChartLabels
    },
    computePiecewiseValues (cell, histFreqRaw) {
      const Mend = constants.BURST_SIZE * constants.N_BURSTS * constants.POOL_SIZE
      let piecewiseFreq = []
      let piecewiseX = []
      // fill piecewise distributions
      histFreqRaw.forEach((freq, i) => {
        let labelVal = this.computeLabelval(cell, i)

        piecewiseFreq.push(i === 0 ? (freq/Mend*100) : (freq/Mend*100 + piecewiseFreq[i-1]))
        piecewiseX.push(labelVal)
        
      })
      // fix possible float point error on highier item of distribution
      piecewiseFreq[piecewiseFreq.length-1] = 1
      return [piecewiseFreq, piecewiseX]
    },
    buildPiecewiseString (cell) {
      // build the piecewise distribution string representation
      let [piecewiseFreq, piecewiseX] = this.computePiecewiseValues(cell, cell.hist)
      return [
        piecewiseFreq.reduce((a, c) => a + '|' + c.toExponential(3), '').slice(1),
        piecewiseX[0].toExponential(3)+ '|' + piecewiseX[piecewiseX.length-1].toExponential(3)
      ].join(';')
    },
    destroyAllCharts () {
      this.notebook.cells.forEach(cell => {
        let chartId = cell.id

        if (this.histCharts[chartId] && !this.histCharts[chartId].animating) {
          this.histCharts[chartId].destroy()
          delete this.histCharts[chartId]
        }
      })
    },
    setHistogramAll () {

      this.$nextTick(() => {
                
        this.$data.notebook.cells.forEach((cell) => {
          
          // register chart obj
          let histChart = this.setHistogramObject(cell)

          // temp var of animatiion definitions
          let chartAnimation = histChart.options.animation

          // disable animation to reduce performance overhead
          histChart.options.animation = false
          this.setHistogram(cell)

          // restore animation
          histChart.options.animation = chartAnimation

        })        
      })
    }
  },  
  computed: {
  }
}