import { Chart } from 'chart.js';
import constants from './constants.js'

const hasSameTags = (a, b) => {
  return JSON.stringify((a || []).sort((a, b) => a-b)) === JSON.stringify((b || []).sort((a, b) => a-b))
}

export default {
  methods: {
    setTrendChart (replaceDatasets) {
      if (['Range', 'Trend'].indexOf(this.chartType) === -1) return
      
      // series label that ends with this suffix will not appear on chart labels
      const filterSuffix = ' __filter__'

      const whiteColor = '#E1E1E1'
      const gridLineColor = '#172431'
      const redColor = '#B80D2F'
      const animationDuration = 100

      
      // fix unselected chartSelectedVar
      if ((this.visibleVars.indexOf(this.chartSelectedVar) === -1) && this.visibleVars.length) {
        this.chartSelectedVar = this.visibleVars[0]
        // return as this methos will be triggered again by watcher
        return
      }

      // fix unselected chartTrendCellId
      if ((this.chartType === 'Trend') && (this.trendChartRefCellInfo.map(item => item.cellId).indexOf(this.chartTrendCellId) === -1) && this.trendChartRefCellInfo.length) {
        this.chartTrendCellId = this.trendChartRefCellInfo[0].cellId
        // return as this methos will be triggered again by watcher
        return
      }      
      

      this.trendChart = this.trendChart || new Chart(this.$refs.trendChart.getContext('2d'), {
        type: 'line',
        datasets: null,
        fontColor: whiteColor,
        data: {
          datasets: []
        },
        options: {
          animation: {
              duration: animationDuration
          },
          legend: {
            labels: {
              fontColor: whiteColor,
              fontSize: 14,
              usePointStyle: true,
              filter: function(item) {
                let toHideLegend = !item.text.endsWith(filterSuffix)
                return toHideLegend
              }
            }
          },
          maintainAspectRatio: false,
          responsive: true,
          scales: {
            yAxes: [{
              scaleLabel: {
                labelString: '',
                display: true
              },
              type: 'linear', //  linear,logarithmic
              ticks: {
                fontColor: whiteColor,
              },
              gridLines: {
                zeroLineColor: gridLineColor,
                color: gridLineColor,
                
                drawTicks: false,
                drawBorder: false,
                drawOnChartArea: true
              },
            }],
            xAxes: [{
              scaleLabel: {
                labelString: '',
                display: true
              },
              type: 'linear',
              distribution: 'series',
              ticks: {
                //autoskip: true,
                //maxTicksLimit: 4,
                //autoSkipPadding: 30,
                fontColor: whiteColor

              },
              gridLines: {
                color: gridLineColor,
                lineWidth: 1,
                drawBorder: false,
                drawTicks: true,
                drawOnChartArea: false
              }
            }]
          }
        }
      })


      let dataSource = null
      let units = ''
      let unitlessSymbol = '1' // https://physics.stackexchange.com/questions/59978/is-there-a-symbol-for-unitless#60007
      // set Range chart options
      if (this.chartType === 'Range') {
        delete this.trendChart.options.scales.xAxes[0].ticks.callback

        dataSource = this.visibleCellsByTagsGroups
        
        let unitsArr = dataSource.map(item => item.cells).flat().map(item => item.unit).reduce((a, c) => a.includes(c) ? a : !a.push(c)||a, [])
        
        units = unitsArr.length === 1 ? (unitsArr[0] || unitlessSymbol) : unitsArr.map(item => item || unitlessSymbol).toString()

        this.trendChart.options.scales.xAxes[0].scaleLabel.labelString = 'Point ('+ units + ')'
      }

      // set Trend chart options
      if (this.chartType === 'Trend') {
        if (!this.trendChartCells) return        

        // format labels as date
        this.trendChart.options.scales.xAxes[0].ticks.callback = function(value) {
          return new Date(new Date(value).getTime()).toISOString().split('T')[0]
        }
        
        //this.chartTrendCellId
        dataSource = [
          {
            name: this.trendChartRefCellInfo.filter(item => item.cellId === this.chartTrendCellId)[0].desc,
            cells: this.trendChartCells
          }
        ]
        
        units = this.trendChartCells[0] ? this.trendChartCells[0].unit : unitlessSymbol
        this.trendChart.options.scales.xAxes[0].scaleLabel.labelString = 'Date'
      }
      
      this.trendChart.options.scales.yAxes[0].scaleLabel.labelString = 'Output ('+ (units || unitlessSymbol) + ')'


      if (replaceDatasets) {
        delete this.trendChart.data.datasets
        this.trendChart.options.animation.duration = 0
        this.trendChart.data.datasets = []
      }

      if (!dataSource) return

      const colors = [
        '#a6e22e',
        '#ae81ff',
        '#f92672',
        '#66d9ef',
        '#e6db74',
        '#fd971f',
        '#204a87',
      ]
      
      // overwrite legend onClick event
      var trendChart = this.trendChart
      this.trendChart.options.legend.onClick = function(_a, label) {

        // check for complementar lines and bars
        trendChart.data.datasets.filter(item => {
          
          let match = item.label === label.text 
            || (item.label === label.text + filterSuffix) // math complementar error bar
            || (item.label === label.text + ' Hlimits' + filterSuffix) // match limits line
            || (item.label === label.text + ' Llimits' + filterSuffix) // match limits line

          return match
        }).forEach(item => {
          item.hidden = !item.hidden
        })        
        trendChart.update()
      }

      

      let errorLines = dataSource.map((group, i) => {
        return Object({
          borderWidth: 3,
          backgroundColor: colors[i],
          borderColor: colors[i],
          label: group.name,
          cubicInterpolationMode: 'monotone',
          data: group.cells.map(cell => 
            Object({
              x: this.xValueForChartType(cell),
              y: parseFloat(cell.y)
            })
          ),
          fill: false
        })
      })

      let uBars = dataSource.map((group, i) => {
        return Object({
          //borderWidth: 0.8,
          borderColor: '#B3B3B3FF',
          backgroundColor: colors[i],
          label: group.name + filterSuffix, // used to filter label. see: https://stackoverflow.com/questions/47084144/is-it-possible-in-chartjs-to-hide-certain-dataset-legends
          pointStyle: 'line',
          radius: 10,
          data: group.cells.reduce((a, cell) => {
            
            // upper
            a.push(Object({
                x: this.xValueForChartType(cell),
                y: parseFloat(cell.pcih)
            }))

            // botton
            a.push(Object({
                x: this.xValueForChartType(cell),
                y: parseFloat(cell.pcil)
            }))

            a.push(Object({
                x: undefined,
                y: parseFloat(cell.pcil)
            }))
            

            return a

          }, []),
          fill: false
        })
      })

      // limits datasets

      const limBaseObj = Object({
        borderColor: redColor,
        radius: undefined,
        pointStyle: 'line',
        borderWidth: 0.8,
        backgroundColor: undefined,
        label: undefined,
        borderDash: [4,2],
        fill: false,
        data: null
      })

      const singlePointLimRadius = 14

      let limHLines = dataSource.map((group, i) => {
        let data = group.cells.reduce((a, cell) => {
          // upper
          a.push(Object({
              x: this.xValueForChartType(cell),
              y: cell.limH
          }))
          return a

        }, [])

        return Object.assign(
          {}, limBaseObj,
          {
            data: data,
            backgroundColor: colors[i],
            label: group.name + ' Hlimits' + filterSuffix,
            radius: data.length > 1 ? 0 : singlePointLimRadius
          })
      })

      let limLLines = dataSource.map((group, i) => {
        let data = group.cells.reduce((a, cell) => {
          // upper
          a.push(Object({
              x: this.xValueForChartType(cell),
              y: cell.limL
          }))
          return a

        }, [])

        return Object.assign(
          {}, limBaseObj,
          {
            data: data,
            backgroundColor: colors[i],
            label: group.name + ' Llimits' + filterSuffix,
            radius: data.length > 1 ? 0 : singlePointLimRadius
          })
      })

      // join datasets

      let datasets = [errorLines, uBars, limLLines, limHLines].reduce((a, c) => a.concat(c), [])

      // update or registres new dataset
      datasets.forEach(ds => {
        let match = this.trendChart.data.datasets.filter(chartDs => chartDs.label === ds.label)[0]
        if (match) {
          match.data = ds.data
        } else {
          this.trendChart.data.datasets.push(ds)
        }
      })

      this.trendChart.update()
      this.trendChart.options.animation.duration = animationDuration
      return this.trendChart
    },
    selectedVarValue (cell) {
      if (!this.cellsVarValuesObjects[cell.id]) return
      return this.cellsVarValuesObjects[cell.id][this.chartSelectedVar]
    },
    xValueForChartType (cell) {
      if (this.chartType === 'Range') {
        return this.selectedVarValue(cell)
      }

      if (this.chartType === 'Trend') {
        let date = new Date(this.notebook.wbs.filter(wb => wb.id === cell.wb)[0].date)
        return date.getTime()
      }      
    }
  },  
  computed: {
    trendChartRefCellInfo () {
      return this.shownCells.filter((cell) => this.cellsVarValuesObjects[cell.id] && 
        (this.selectedVarValue(cell) !== undefined)
      ).map(cell => {
        let desc = '' + this.selectedVarValue(cell)
        desc += cell.unit ? ' ' + cell.unit : ''
        
        desc += cell.tags ? '  [' + cell.tags.toString() + ']' : ''

        return {desc: desc, cellId: cell.id}
      })
    },
    trendChartRefCell () {
      return this.shownCells.filter(cell => cell.id === this.chartTrendCellId)[0]
    },
    trendChartCells () {
      if (!this.trendChartRefCell) return

      return this.notebook.cells.filter(cell => {
        //if (cell.wb === this.trendChartRefCell.wb) return false
        if (!this.trendChartRefCell.wb) return false
        if (!this.filteredDataAllWb[cell.id]) return false
        if (cell.unit !== this.trendChartRefCell.unit) return false
        if (!hasSameTags(cell.tags, this.trendChartRefCell.tags)) return false
        if (!hasSameTags(cell.tags, this.trendChartRefCell.tags)) return false      
        
        return true
      }).map(cell => {
        
        let valRef = this.selectedVarValue(this.trendChartRefCell)
        let valX = this.selectedVarValue(cell)

        // Rescue from NaN
        if (valRef <= constants.JS_ZERO) {
          return [0, cell]
        }
        
        // generate tuples to sort by % difference
        return [Math.abs((valX - valRef) / valRef), cell]
      })

      // get only values comparable by a threshold
      .filter(item => item[0] <= 0.01)

      // sort by value used to be compared, so we get the best match first
      .sort((a, b) => a[0] - b[0])

      // buil object with best match for each workbook, with only one per workbook
      .reduce((a, c) => {
        if (a.map(item => item.wb).indexOf(c[1].wb) === -1) {
          a.push(c[1])
        }
        return a
      }, [])
    }
  }
}